import React from 'react';
import './index.css';

/**
 * @param 
 * [align] <String>  center flex-start | flex-end 
 */
export default ({ children, style = {}, className = '', align = 'center' }) => {
  const defaultStyle = {
    alignItems: align,
    ...style,
  }
  const defaultClassName = `Kako-Layout-Flex ${className}`;
  return <div style={ defaultStyle } className={ defaultClassName }>
    { children }
  </div>
}